@media (min-width: 979px) {
  body {
    padding-top: 60px;
  }
}
body.admin { padding-top: 0px; }
body.home { padding-top: 40px; }
.top-margin { margin-top: 50px; }

.panel-heading,
.panel-title {
  color: #fff;
}
.panel {
  .box-shadow(10px 10px 10px fade(@gray-base, 30%));
}

#x { background: url(/images/x.png) right -50px no-repeat; }
.home h2 { color: #8dc63f; font-size: 18px; font-weight: bold; margin: 45px 0 10px 0; }
.home label { color: #999; font-weight: normal; display: block;}
.home input[type="text"], .home input[type="email"] { width: 200px; }
h2.mailchimp { margin-top: 90px; }
p a { color: #c00; }
p a:hover { color: #f00; }
#splash { background: url(/images/home-map.jpg); height: 300px; width: 100%; padding: 25px 0 0 0; margin: 0; }
#splash h1 { color: #241d4e !important; font-size: 20px; font-weight: bold; }

.form-group.required .control-label:after { content:" *"; color:#ff0000; }
.form-group label { font-weight: bold; }

#logo-list { margin-right: 0; padding: 0; }
#logo-list li { display: inline; padding: 0; width: 80px; }
#social-list { float: right; width: 120px; }
#social-list li { float: left; list-style: none; width: 40px; }
#footer-home { margin-top: 305px; margin-bottom: 25px; }
.home #footer-home { margin-top: 35px; margin-bottom: 25px; }

.participate { display: inline-block; background: #be1e2d; font-weight: 900; color: #fff; font-size: 28px; line-height: 26px; padding: 10px 15px 5px; margin: 20px 0; text-transform: uppercase }
.participate span { color: #231c50; }
.participate:hover { color: #231c50; text-decoration: none; }
.participate:hover span { color: #fff; }
.description { font-size: 130%; line-height: 150%; font-style: italic; margin-bottom: 15px; }
.complete { background: #ccc; width: 100px; display: inline-block;
  background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
  background-image: -o-linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
